.app{
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

#game {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 600px;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 24px;
  text-align: center;
  cursor: crosshair;
  
}

#game button{
  cursor: crosshair;
}


.circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: red;
  animation: shrink 0.5s infinite;
  border: none;
}

@keyframes shrink {
  0% {
    width: 30px;
    height: 30px;
  }

  50%{
    width: 15px;
    height: 15px;
  }

  100% {
    width: 30px;
    height: 30px;
  }
}

